<template>
  <div class="menu" :class="[{ isWhite: bIsWhite }]">
    <ul>
      <li v-for="(item, i) in aMenu" :key="item + i">
        <a :href="item.link">{{ item.name }}</a>
      </li>
    </ul>
  </div>
</template>

<script setup>
import json from "@/api/json/bbb.json";
import $ from "jquery";
import { reactive, ref } from "vue";
const aMenu = reactive(json.menu);
const bIsMobi = $(window).width() <= 768;
const bIsWhite = ref(false);

if (!bIsMobi) {
  $(window).scroll(() => {
    bIsWhite.value = $(window).scrollTop() > 200;
  });
}
</script>

